@import "_init/vars";
@import "_init/mixin";
@import "~@_nu/css-acss/lib/_acss.scss";

/**
 *  `*` 代表后面的数字
 */
$rem: 1/16 * 1rem; // 换算单位的方式
@include ACSS; // 核心的 acss 代码
@include ACSS_(margin, (0, 4, 8, 10, 14, 18, 20, 24, 28, 36), $rem); // .mt*, .mr*, .mb*, .ml*
@include ACSS_(padding, (0, 4, 8, 10, 14, 18, 20, 24, 28, 36), $rem); // .pt*, .pr*, .pb*, .pl*
@include ACSS_(font-size, (0, 8, 10, 12, 14, 16, 18, 20, 24, 28, 36), $rem); // .fs*
@include ACSS_(line-height, (14, 16, 20, 24, 36), $rem); // .lh*
@include ACSS_(font-weight, (500, 600, 700)); // .fw*
/**
 * .@name{ color:@value; }
 */
@include ACSS_(
  color,
  (
    c_l: $c_l,
    c_m: $c_m,
    c_primary: $c_primary,
    c_secondary: $c_secondary,
    c_gray1: $c_gray1
  )
);


/*
 * 375px ～ 414px
 * 16px ~ 18px
 */
html {
  background-color: #09080C;
  font-size: 16px;
  // font-size: calc(100% + 2 * (100vw - 360px) / 39);
  // font-size: calc(16px + 2 * (100vw - 360px) / 39);
  font-size: calc(100% + 2 * (100vw - 375px) / 39);
  font-size: calc(16px + 2 * (100vw - 375px) / 39);
  line-height: 1.5;
  overflow-x: hidden;

  // 414px以下自适应隐藏(pc需要隐藏的内容)
  .pc-h {
    display: none;
  }

  .pc-hib {
    display: none;
  }

  .pc-hf {
    display: none;
  }
}

/*
 * 414px ～ 750px
 * 18px ~ 22px
 * 每 100 像素宽字体增加 1px,
 * 会被下面的 750px 拦截
 */
@media screen and (min-width: 414px) {
  html {
    font-size: calc(112.5% + 4 * (100vw - 414px) / 586);
    font-size: calc(18px + 4 * (100vw - 414px) / 586);
  }
}

/*
 * 750px 开始是每100像素0.5px增加
 * 750px 是 20.5px
 * 1000px 刚好是 22px
 */
@media screen and (min-width: 750px) {
  html {
    background-color: #fff;
    font-size: calc(137.5% + 6 * (100vw - 1000px) / 1000);
    font-size: calc(22px + 6 * (100vw - 1000px) / 1000);

    // pc需要展示的内容
    .pc-h {
      display: block;
    }
    
    .pc-hib {
      display: inline-block;
    }

    .pc-hf {
      display: flex;
    }

    // m站需要隐藏的内容
    .m-h {
      display: none !important;
    }
  }
}

/* see https://stackoverflow.com/questions/11289166/chrome-on-android-resizes-font */
html * {
  max-height: 1000000px;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
  color: inherit;
}

/* body 全局样式 */
body {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Helvetica", "Arial",
    sans-serif;
  font-size: 0;
  color: $c_l;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.05);
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
}

/* 自定义样式 */
.vat {
  vertical-align: top;
}
.w100p {
  width: 100%;
}

.w33p {
  width: 33.33%;
}
/* 多行文本点点点 */
.ell2row,
.ell3row {
  overflow: hidden;
}
.ell2row {
  -webkit-line-clamp: 2;
}
.ell2row.lh20 {
  max-height: rem(40);
}
.ell3row.lh20 {
  max-height: rem(60);
}
.ell2row.lh16 {
  max-height: rem(32);
}
.ell2row.lh24 {
  max-height: rem(48);
}

/* 带宽度的block元素左右居中 */
.auto {
  margin-left: auto;
  margin-right: auto;
}

/* 不影响seo的隐藏元素 */
.hide {
  position: fixed;
  clip: rect(0 0 0 0);
  top: 0;
}

/* 全局样式 */
.g_wrap {
  margin-left: rem(10);
  margin-right: rem(10);
}
.g_row {
  padding-left: rem(10);
  padding-right: rem(10);
}

.g_hr {
  position: relative;
}
.g_hr:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  height: 1px;
  background-color: #d7d8e0;
  -webkit-transform: translateZ(0) scaleY(0.5);
  transform: translateZ(0) scaleY(0.5);
}

.l10 {
  left: 10%;
}

a {
  text-decoration: none;
  outline: none;
}

i {
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

ul,
ol {
  list-style: none;
}

button {
  border: none;
  outline: none;
}

