/**
 * @author ziven
 * @desc base mixin
 */

/**
 * [phBg 添加fontbase]
 */

@function rem($value) {
  @return $value / 16 * 1rem;
}

@mixin font-face($name, $path) {
  @font-face {
    font-family: $name;
    src: url("#{$path}.woff") format("woff");
    src: url("#{$path}.eot"),
      url("#{$path}.eot?#font-spider") format("embedded-opentype"),
      url("#{$path}.ttf") format("truetype"), url("#{$path}.svg") format("svg");
    font-weight: normal;
    font-style: normal;
  }
}

@mixin margin($gutters) {
  @each $gutter in $gutters {
    .mt#{$gutter} {
      margin-top: $gutter/16 * 1rem;
    }
    .mr#{$gutter} {
      margin-right: $gutter/16 * 1rem;
    }
    .mb#{$gutter} {
      margin-bottom: $gutter/16 * 1rem;
    }
    .ml#{$gutter} {
      margin-left: $gutter/16 * 1rem;
    }
  }
}

@mixin padding($gutters) {
  @each $gutter in $gutters {
    .pt#{$gutter} {
      padding-top: $gutter/16 * 1rem;
    }
    .pr#{$gutter} {
      padding-right: $gutter/16 * 1rem;
    }
    .pb#{$gutter} {
      padding-bottom: $gutter/16 * 1rem;
    }
    .pl#{$gutter} {
      padding-left: $gutter/16 * 1rem;
    }
  }
}

/**
 * [phBg 2倍图]
 */

@mixin media2x() {
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    @content;
  }
}

/**
 * [dot 多行点点点]
 * @param  {[type]} $row [行数]
 */
@mixin dot($row) {
  display: -webkit-box;
  overflow: hidden;
  word-wrap: break-word;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $row;
}
/**
 * [clearfix 清除浮动]
 */
@mixin clearfix() {
  &:after {
    display: table;
    content: "";
    clear: both;
  }
}
/**
 * [clearfix 单行点点点]
 */
@mixin ell() {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
/**
 * [cell 剩余填充]
 */
@mixin cell() {
  display: table-cell;
  *display: inline-block;
  width: 2000px;
  *width: auto;
}
/**
 * [hide clip隐藏元素]
 */
@mixin hide() {
  position: absolute;
  clip: rect(0 0 0 0);
}
/**
 * [flex flex布局]
 */
@mixin flex() {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-pack: justify;
  box-pack: justify;
  -webkit-overflow-scrolling: touch;
}
/**
 * [flexItem flex元素]
 */
@mixin flexItem() {
  display: inline-block;
  flex: 1;
  -webkit-box-flex: 1;
  -moz-flex-grow: 1;
  -webkit-flex-grow: 1;
  flex-grow: 1;
}
/**
 * [fill 覆盖父元素]
 */
@mixin fill() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
