@charset "utf-8";
@mixin ACSS {
  /*
  * # 命名规则
  * 1. 只取首字母 .db{ display:block; };
  * 2. 有数字直接连接 .mb10{ margin-bottom:10px; };
  * 3. 百分号用p（percent）表示 .w100p{ width:100%; };
  * 4. 小数点用d（dot）表示 .lh1d2{ line-height:1.2};
  * 5. 有想要自定义的样式，用_分割单词;
  */

  /* ## scruct  */
  .di {
    display: inline;
  }
  .db {
    display: block;
  }
  .dn {
    display: none;
  }
  .dib {
    display: inline-block;
  }
  .oh {
    overflow: hidden;
  }
  .oa {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
  .w100p {
    width: 100%;
  }
  .h100p {
    height: 100%;
  }
  .vam {
    vertical-align: middle;
  }

  /* ### scruct position */
  .pr {
    position: relative;
  }
  .pf {
    position: fixed;
  }
  .pa {
    position: absolute;
  }
  .z1 {
    z-index: 1;
  }
  .t0 {
    top: 0;
  }
  .r0 {
    right: 0;
  }
  .b0 {
    bottom: 0;
  }
  .l0 {
    left: 0;
  }
  .t50p {
    top: 50%;
  }
  .l50p {
    left: 50%;
  }
  .l100p {
    left: 100%;
  }
  .t100p {
    top: 100%;
  }
  .mla {
    margin-left: auto;
  }
  .mra {
    margin-right: auto;
  }

  /* ### scruct float */
  .fl {
    float: left;
    display: inline;
  }
  .fr {
    float: right;
  }

  /* ### struct flex */
  .df {
    display: flex;
  }
  .dif {
    display: inline-flex;
  }
  .fa {
    flex: auto;
  }
  .aic {
    align-items: center;
  }
  .aife {
    align-items: flex-end;
  }
  .aifs {
    align-items: flex-start;
  }
  .fdr {
    flex-direction: row;
  }
  .fdc {
    flex-direction: column;
  }
  .fww {
    flex-wrap: wrap;
  }
  .fs1 {
    flex-shrink: 1;
  }
  .f1 {
    flex: 1;
    min-width: 0;
  }
  //.fwn{ flex-wrap:nowrap; } 和 .fwn{ font-weight: normal } 重名所以忽略掉
  .jcsb {
    justify-content: space-between;
  }
  .jcsa {
    justify-content: space-around;
  }
  .jcc {
    justify-content: center;
  }
  .jcfe {
    justify-content: flex-end;
  }

  /* ## style =================== */
  .vh {
    visibility: hidden;
  }
  .br100p {
    border-radius: 100%;
  }

  /* ### style font */
  /* 小型大写 */
  .fvsc {
    font-variant: small-caps;
    text-transform: lowercase;
  }
  .fs0 {
    font-size: 0;
  }
  .fwn {
    font-weight: 400;
  }
  .fwb {
    font-weight: 700;
  }
  .fsi {
    font-style: italic;
  }
  .wsn {
    white-space: nowrap;
  }
  .wwbw {
    word-wrap: break-word;
    word-break: break-all;
  }
  .lh1d5 {
    line-height: 1.5;
  }
  .lh1d8 {
    line-height: 1.8;
  }
  .tar {
    text-align: right;
  }
  .tac {
    text-align: center;
  }
  .tal {
    text-align: left;
  }
  .ttu {
    text-transform: uppercase;
  }
  .ttc {
    text-transform: capitalize;
  }
  .ttn {
    text-transform: none;
  }
  .ttl {
    text-transform: lowercase;
  }
  .tdn {
    text-decoration: none;
  }

  /* ### style color */
  .c_fff {
    color: #fff;
  }
  .c_000 {
    color: #000;
  }
  .bc_fff {
    background-color: #fff;
  }

  /*
  * 以下属性因为太常用所有放在了这里
  * 这里的命名并没有严格按照上面的规则
  */


  /* 清除浮动 */
  .clearfix {

    &:after {
      display: table;
      content: '';
      clear: both;
    }
  }

  /* 文字超出一行点点点 */
  .ell {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}


@mixin ACSS_mapList($list, $prop, $preName:'', $unit:false) {
  @each $name,
  $value in $list {

    // 如果是二级数组
    @if $value {
      .#{$name} {
        #{$prop}: if($unit, $value * $unit, $value);
      }
    } @else {
      .#{$preName}#{$name} {
        #{$prop}: if($unit, $name * $unit, $name);
      }
    }
  }
}

$NAME_MAP: (
        'font-size': fs,
        'font-weight': fw,
        'line-height': lh
);

@mixin ACSS_($name:false, $map:(), $unit:false) {
  @if $name == 'margin' {
    @include ACSS_mapList($map, margin-top, mt, $unit);
    @include ACSS_mapList($map, margin-bottom, mb, $unit);
    @include ACSS_mapList($map, margin-right, mr, $unit);
    @include ACSS_mapList($map, margin-left, ml, $unit);
  } @else if $name == 'padding' {
    @include ACSS_mapList($map, padding-top, pt, $unit);
    @include ACSS_mapList($map, padding-right, pr, $unit);
    @include ACSS_mapList($map, padding-bottom, pb, $unit);
    @include ACSS_mapList($map, padding-left, pl, $unit);
  } @else if $name == 'font-size' {
    @include ACSS_mapList($map, font-size, fs, $unit);
  } @else if $name == 'line-height' {
    @include ACSS_mapList($map, line-height, lh, $unit);
  } @else if $name == 'font-weight' {
    @include ACSS_mapList($map, font-weight, fw);
  } @else {
    @include ACSS_mapList($map, $name);
  }
}
