@import '../../styles/init.scss';

.yux-join {
  font-size: rem(12);
  color: #fff;
  padding: rem(34) rem(16) rem(32);
  background-color: #202020;

  h1 {
    font-size: rem(18);
    line-height: rem(25);
    text-align: center;
    padding-top: rem(40);
  }

  h3 {
    text-align: center;
    font-size: rem(14);
    line-height: rem(20);
    margin: rem(15) 0 rem(10);
  }

  a {
    color: #347EFF;
    text-decoration: underline;
  }

  &-content {
    color: #989898;
    line-height: rem(17);
  }

  &-contact {
    color: #818081;
    font-size: rem(14);
    line-height: rem(20);
    margin-top: rem(15);
  }
}